<template>
    <div>
        <div class="row">
            <p class="tit">添加菜肴</p>
        </div>
        <div class="row">
            <p>菜肴名称</p>
        </div>
        <div class="row">
            <el-input v-model="options.name" placeholder="菜肴名称"></el-input>
        </div>
        <div class="row">
            <p>展示图</p>
        </div>
        <div class="row">
            <el-input v-model="options.pic" placeholder="展示图"></el-input>
        </div>
        <div class="row">
            <p>荤素</p>
        </div>
        <div class="row">
            <el-radio class="radio" v-model="options.meat" label=0>荤素搭配</el-radio>
            <el-radio class="radio" v-model="options.meat" label=1>主荤</el-radio>
            <el-radio class="radio" v-model="options.meat" label=2>主素</el-radio>
        </div>

        <div class="row">
            <p>口味</p>
        </div>
        <div class="row">
            <el-radio class="radio" v-model="options.taste" label="1">酸</el-radio>
            <el-radio class="radio" v-model="options.taste" label="2">甜</el-radio>
            <el-radio class="radio" v-model="options.taste" label="3">辣</el-radio>
            <el-radio class="radio" v-model="options.taste" label="4">鲜</el-radio>
        </div>
        <div class="row">
            <p>时间段</p>
        </div>
        <div class="row">
            <el-radio class="radio" v-model="options.time" label="0">不限</el-radio>
            <el-radio class="radio" v-model="options.time" label="1">早餐</el-radio>
            <el-radio class="radio" v-model="options.time" label="2">午餐</el-radio>
            <el-radio class="radio" v-model="options.time" label="3">晚餐</el-radio>
        </div>
        <div class="row">
            <p>
                <el-button type="primary" @click="add">添加</el-button>
            </p>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'hello',
        data() {
            return {
                options: {
                    name: "",
                    taste: "1",
                    pic: '',
                    meat: "0",
                    time: "0",
                }
            }
        },
        methods: {
            add() {
                if (!this.options.name) {
                    this.$message({
                        message: '请输入名称',
                        type: 'warning'
                    })
                } else if (!this.options.pic || !this.options.pic.match(/^https?:\/\/.+\.(jpg|gif|png)/)) {
                    this.$message({
                        message: '错误的展示图链接',
                        type: 'warning'
                    })
                } else {
                    this.axios.post(apiUrl + '/eatApi/dish/add', this.options).then(res => {
                        if (res.data.msgcode == 1) {
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            })
                        } else {
                            this.$message({
                                message: res.data.msg,
                                type: 'warning'
                            })
                        }
                    })
                }
            }
        }
    }

</script>

<style scoped>
    .el-col p {
        color: #1D8CE0;
    }
</style>


